/**
 * # Clase Direccion
 * 
 * Esta clase representa una direccin postal, permitiendo almacenar y gestionar informacin relacionada con la ubicacin de una persona o lugar.
 */
export class Direccion {
    /**
     * ## Constructor de la clase Direccion
     * 
     * Inicializa una nueva instancia de la clase **Direccion** con los datos proporcionados.
     * 
     * @param {string} calle - El nombre de la calle.
     * @param {number|string} numero - El nmero de la calle. Puede incluir letras como "5B".
     * @param {string} piso - El nmero o descripcin del piso (por ejemplo, "3A").
     * @param {string} codigoPostal - El cdigo postal asociado a la direccin.
     * @param {string} provincia - La provincia donde se encuentra la direccin.
     * @param {string} localidad - La localidad o ciudad donde se encuentra la direccin.
     */
    constructor(calle, numero, piso, codigoPostal, provincia, localidad) {
        this.calle = calle; // Nombre de la calle
        this.numero = numero; // Nmero de la calle
        this.piso = piso; // Piso o descripcin del piso
        this.codigoPostal = codigoPostal; // Cdigo postal
        this.provincia = provincia; // Provincia
        this.localidad = localidad; // Localidad o ciudad
    }

    /**
     * ## Mtodo: toString
     * 
     * Este mtodo genera una representacin en cadena de la direccin postal. 
     * Incluye la calle, nmero, piso, cdigo postal, localidad y provincia.
     * 
     * @returns {string} - Representacin en cadena de la direccin.
     */
    toString() {
        return `${this.calle} ${this.numero}, Piso ${this.piso}, ${this.codigoPostal} ${this.localidad}, ${this.provincia}`;
    }
}
